<template>
  <div id="Login" class="r-vh-100-min r-isRelative" >

    <div class="r-position-center r-isExpand">
      <div class="r-rad-4 r-box r-white-bg r-p-16 r-wd-400-max r-isExpand r-mlr-auto">
          <h2 class="r-mb-16">Selamat Datang</h2>
          <form novalidate >
            
            <InputCustom
              v-model="item.username"
              label="Username"
              :error="error.username"
              :errorMsg="errorMsg.username"
              @onFocus="resetError('username')"
            >
            </InputCustom>

            <InputCustom
              v-model="item.password"
              label="Password"
              :error="error.password"
              :errorMsg="errorMsg.password"
              @onFocus="resetError('password')"
              :rightIcon="true"
              :type="show ? 'text' : 'password'"
            >
              <template #rightIcon>
                <!-- <img :src="require(`@/assets/icons/login/eye${show ? 'Close' : 'Open'}.svg`)" alt="icons" @click.prevent="show = !show"> -->
              </template>
            </InputCustom>

            <ButtonCustom class="r-isExpand r-mt-20" @click.prevent="login()" :isLoading="isLoading">
              Login
            </ButtonCustom>
          </form>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions } from 'vuex'
import metatag from '@/mixins/metatag'
import common from '@/mixins/common'
import ButtonCustom from '@/components/helper/Button'
import InputCustom from '@/components/helper/Input'
import validation from '@/mixins/validation'

export default {
    mixins: [metatag, common, validation],
    components: {
      ButtonCustom,
      InputCustom
    },
    mounted() {
      this.setMetaTag()
    },
    data: () => ({
        module: 'auth',
        item: {
          username: null,
          password: null,
        },
        error: {
          username: false,
          password: false,
        },
        errorMsg:{
          username: 'Tidak boleh kosong',
          password: 'Tidak boleh kosong',
        },
        show: false,
        isLoading: false,
        message: 'Tidak valid',
    }),
    methods:{
        ...mapActions({
          loginUser: 'auth/login'
        }),
        resetError(value){
            this.error[value] = false
        },
        async validation(){
          const field = ['username', 'password']
          return await this.validateForm(field,this.item)
        },
        async login() {
            const errorcount = await this.validation()
            if(errorcount == 0){
                this.isLoading= true
                await this.loginUser(this.item)
                const response = this.getResponse()
                if(response.status == 0){
                  this.setLoginError(response.message)
                }else{
                  setTimeout(function(){window.location.reload()}, 3000)
                }
            }else{
                this.isLoading= false
                this.message = 'Mohon periksa kembali'
            }
        },
        setLoginError(message){
          this.isLoading = false

          this.error.username = true
          this.error.password = true

          this.errorMsg.username = message
          this.errorMsg.password = message
        }
    }
}
</script>
